import React, { Component } from 'react';
import "../css/App.css";
import jQuery from "jquery";
import { Subject } from './ChooseSubject';
import { Client } from './ScanWizard';
import { KeyValue } from '../types';


export interface ReportSummary {
  token:string;
  clientInfo: string;
  subjectInfo: string;
  productInfo: string;
  voucherInfo: string;
  subTotal: number;
  tax: number;
  total:number;
}

export interface CreateReportProps {
  subject: Subject;
  sku:string|null;
  client: Client;
  licenceKey:string|null;
  stepButtonStatus(prevStatus: boolean, nextStatus: boolean): void;
  setFileId(id:string):void;
}

export interface CreateReportState {
  reportSummary: ReportSummary | null;
  isGettingReportSummary:boolean;
  isCreatingReport:boolean;
  fileId:string;
}

export default class CreateReport extends Component<CreateReportProps, CreateReportState> {
  watchTimeout: any;
  constructor(props: CreateReportProps) {
    super(props)
    this.state = {
      reportSummary:null,
      isGettingReportSummary:false,
      isCreatingReport:false,
      fileId:""
    }
    this.startReportCreation = this.startReportCreation.bind(this);
  }

  componentDidMount() {
    this.props.stepButtonStatus(true, false)
    this.startReportCreation()
    //  this.getQuotation(this.props)
  }

  render() {
    console.log("Payment component State :", this.state)
    console.log("Payment component Props :", this.props)
    let key = 0;
    return (
      <div>
        {this.state.isCreatingReport && 
          <div className="justify">
            <div className="paymentComponent">Uw rapport wordt gegenereerd...</div>
            <div className="lds-dual-ring"></div>
          </div>
        }
        {this.state.fileId!=="" && !this.state.isCreatingReport && <div>
          Uw rapport is gecreeerd. <br></br>
          De volgende licentiekey wordt gebruikt voor het downloaden<br></br>
          {this.props.licenceKey}
        </div>}
      
    </div>
    );
  }


  startReportCreation(){
    console.log ("Start Report Creation")
    let data={
      sku:this.props.sku,
      subject:this.props.subject.name,
      client:this.props.client
    }
    this.setState({isCreatingReport:true})

    let componentHandle = this
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-test-firebase-functions-51beb.cloudfunctions.net/startReportCreation",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log(result)
        componentHandle.setState({
          isCreatingReport:false,
          fileId:result.id
        },()=>{
          this.props.setFileId(this.state.fileId)
        })
        this.props.stepButtonStatus(false,true)
      },

      error: (error) => {
        console.log(error)
      }
    })
  }


}