import React, { Component } from 'react';
import jQuery from "jquery";
import { Client } from './ScanWizard'
import { isNull } from 'util';

export interface DownloadProps {
  fileId: string;
  licenseId: string | null;
  sku: string | null;
  emailAddress: string | null;
}

export interface DownloadState {
  reportCreationStatus: string;
  startedTime: Date;
  timedOut: boolean;
  isLicensing: boolean;
  isMailing: boolean;
}


export default class Download extends Component<DownloadProps, DownloadState> {

  watchTimeout: any;

  constructor(props: DownloadProps) {
    super(props)
    this.downloadReport = this.downloadReport.bind(this)
    this.licenseReport = this.licenseReport.bind(this)
    this.state = {
      reportCreationStatus: "paid",
      startedTime: new Date(),
      timedOut: false,
      isLicensing: false,
      isMailing: false
    }
  }

  componentDidMount() {
    this.licenseReport()
  }

  render() {
    return (
      <div className="DownloadForm">
        Rapport Downloaden
          {this.state.isLicensing && <div className="justify">
          <div className="paymentComponent">Uw rapport wordt gelicenseerd...</div>
          <div className="lds-dual-ring"></div>
        </div>
        }
        {this.state.isMailing && <div>
          <div className="paymentComponent">Uw rapport wordt nu gedownload en gemaild naar: {this.props.emailAddress}</div>
        </div>
        }
      </div>
    );
  }

  licenseReport() {
    console.log("Start Report Creation")
    let data = {
      sku: this.props.sku,
      licenseKey: this.props.licenseId,
      id: this.props.fileId,
      email: this.props.emailAddress
    }
    this.setState({ isLicensing: true });

    let componentHandle = this
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-test-firebase-functions-51beb.cloudfunctions.net/licenseReport",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log("RESULT:", result)
        componentHandle.setState({
          isLicensing: false,
          isMailing: result.result === "success"
        })
        if (result.result === "success") {
          console.log("Start DownLoad")
          componentHandle.downloadReport()
        }
      },


      error: (error) => {
        console.log(error)
      }
    })

  }

  testMobile() {
    let isMobile = {
      Android: function () { return navigator.userAgent.match(/Android/i); },
      BlackBerry: function () { return navigator.userAgent.match(/BlackBerry/i); },
      iOS: function () { return navigator.userAgent.match(/iPhone|iPad|iPod/i); },
      Opera: function () { return navigator.userAgent.match(/Opera Mini/i); },
      Windows: function () { return navigator.userAgent.match(/IEMobile/i); },
      any: function () { return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows()); }
    };
    return isMobile

  }

  downloadReport() {
    let view = (this.testMobile().any() !== null)
    let serviceUrl = "https://us-central1-test-firebase-functions-51beb.cloudfunctions.net/downLoadReport"
    console.log(serviceUrl)
    let fileName = this.props.fileId
    this.downloadFile(serviceUrl + "?id=" + fileName + "&emailAddress=" + this.props.emailAddress + "&view=" + view)
  }

  downloadFile(sUrl: string) {
    const isChrome = navigator.userAgent.toLowerCase().indexOf('chrome') > -1;
    const isSafari = navigator.userAgent.toLowerCase().indexOf('safari') > -1;

    //iOS devices do not support downloading. We have to inform user about this.
    if (/(iP)/g.test(navigator.userAgent)) {
      alert('Your device does not support files downloading. Please try again in desktop browser.');
      return false;
    }
    //If in Chrome or Safari - download via virtual link click
    if (isChrome || isSafari) {
      //Creating new link node.
      var link = document.createElement('a');
      link.href = sUrl;

      if (link.download !== undefined) {
        //Set HTML5 download attribute. This will prevent file from opening if supported.
        var fileName = sUrl.substring(sUrl.lastIndexOf('/') + 1, sUrl.length);
        link.download = fileName;
      }

      //Dispatching click event.
      if (document.createEvent) {
        var e = document.createEvent('MouseEvents');
        e.initEvent('click', true, true);
        link.dispatchEvent(e);
        return true;
      }
    }

    // Force file download (whether supported by server).
    if (sUrl.indexOf('?') === -1) {
      sUrl += '?download';
    }

    window.open(sUrl, '_self');
    return true;

  }




}